<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>我的收藏</p>
                <img v-if="one" class="search" src="../../../assets/personal/fold.png" alt="" @click="showCha1">
                <img v-if="two" class="search" src="../../../assets/personal/fold.png" alt="" @click="showCha2">
        </section>
        <section class="middle">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        :maxLength="5"
                        @change="onTabChange"
                ></md-tab-bar>
                <div class="none" v-if="!listLeft.length"  :style="{height: Height}">暂无数据</div>
                <md-scroll-view  v-if="listLeft.length" :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                        v-for="i in listLeft"
                        class="scroll-view-item"
                        :class="{expertItem: searchParams.type === 4, underLine: searchParams.type === 4 }"
                        :key="i.id"
                        @click="$_onItemClick(i)">
                        <div class="imgCover" :class="{imgCoverExpert: searchParams.type === 4}" style="align-items: center;overflow: hidden">
                            <img v-if="searchParams.type === 1 || searchParams.type === 2 || searchParams.type === 3" class="img" :src="i.img" alt="">
                            <img v-if="searchParams.type === 4" class="img expert-img" :src="i.pic" alt="">
                            <img v-if="searchParams.type === 5" class="img" :src="i.logo" alt="">
                            <img v-if="searchParams.type === 6 || searchParams.type === 7" class="img" :src="i.picUrl" alt="">
                        </div>
                        <div class="article">
                            <p v-if="searchParams.type === 5" v-html="i.unitName"></p>
                            <p v-else-if="searchParams.type === 3" v-html="i.name + i.specifications"></p>
                            <p v-else-if="searchParams.type === 6 || searchParams.type === 7" v-html="i.title"></p>
                            <p v-else v-html="i.name"></p>
                            <span v-if="searchParams.type === 6 || searchParams.type === 7"><img src="@/assets/home/icon_time@2x.png" alt="">{{ i.createTime }}</span>
                            <span v-if="searchParams.type === 1 || searchParams.type === 2"><img src="@/assets/home/icon_time@2x.png" alt="">{{ new Date(i.createTime).toLocaleDateString() }}</span>
                            <span v-if="searchParams.type === 3" style="width: 100%" class="genre" v-text="i.unit"></span>
                            <span v-if="searchParams.type === 4" style="width: 100%" class="genre" v-text="i.type"></span>
                            <span v-if="searchParams.type === 4" class="expertTitle" v-text="i.genre"></span>
                            <span v-if="searchParams.type === 5" v-text="roleIdObj[i.roleId]"></span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            <md-picker
                    ref="typePicker"
                    v-model="isshowCha1"
                    :data="cha1"
                    @confirm="onChaConfirm1"
                    title="设备类型"
            ></md-picker>
            <md-picker
                    ref="typePicker"
                    v-model="isshowCha2"
                    :data="cha2"
                    @confirm="onChaConfirm2"
                    title="单位类型"
            ></md-picker>
        </section>
    </main>
</template>

<script>
import { getList, otherGetList } from '@/apiUrl/personal/collection.js'
import { TabBar, Swiper, Picker, Icon, SwiperItem, ScrollView, ScrollViewMore, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [Icon.name]: Icon,
      [Picker.name]: Picker,
    [ScrollViewMore.name]: ScrollViewMore,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 1,
      // tab项
      items: [
        { name: 1, label: '成果', route: 'scienceResult' },
        { name: 2, label: '需求', route: 'technology' },
        { name: 3, label: '设备', route: 'device' },
        { name: 4, label: '专家', route: 'expert' },
        { name: 5, label: '单位', route: 'unit' },
        { name: 6, label: '活动', route: 'expert' },
        { name: 7, label: '项目', route: 'unit' }
      ],
      listLeft: [],
      isFinished: false,
        one: false,
        two: false,
      Height: '900px',
      listRight: [],
      searchParams: {
        type: 1,
        pageSize: 10,
        pageNumber: 1
      },
        isshowCha1: false,
        isshowCha2: false,
      roleIdObj: {
        2: '企业',
        3: '高校',
        4: '服务机构',
        5: '其他单位'
      },
        cha1: [
            [
                { text: '正在对接', value: 1 },
                { text: '对接完成', value: 2 }
            ]
        ],
        cha2: [
            [
                { text: '企业', value: 2 },
                { text: '高校院所', value: 3 },
                { text: '服务机构', value: 4 },
                { text: '其他单位', value: 5 }
            ]
        ]
    }
  },
  mounted () {
    this.getList()
    this.Height = window.innerHeight - 84 + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.listLeft = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
      showCha1 () {
          this.isshowCha1 = true
      },
      showCha2 () {
          this.isshowCha2 = true
      },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
        item.name === 3 ? this.one = true : this.one = false
        item.name === 5 ? this.two = true : this.two = false
      this.searchParams.type = item.name
      if (item.name !== 6 && item.name !== 7) {
        this.searchParams.pageNumber = 1
        this.getList()
      } else if (item.name === 6) {
        const args = {
          type: 1
        }
        otherGetList(args).then((res) => {
          this.listLeft = res.data
        }).catch(err => {
          Toast.failed(err)
        })
      } else {
        const args = {
          type: 2
        }
        otherGetList(args).then((res) => {
          this.listLeft = res.data
        }).catch(err => {
          Toast.failed(err)
        })
      }
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      if (this.searchParams.type !== 6 && this.searchParams.type !== 7) {
        let route = this.items[this.current - 1].route
        this.$router.push({
          path: '/' + route + '/detail',
          query: { id: i.id }
        })
      }
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.listLeft = this.listLeft.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
      // 类型选择确认
      onChaConfirm1 (columnsValue) {
          console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
          this.searchParams.status = columnsValue[0].value
          this.searchParams.pageNumber = 1
          this.getList()
      },
      // 类型选择确认
      onChaConfirm2 (columnsValue) {
          console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
          this.searchParams.status = columnsValue[0].value
          this.searchParams.pageNumber = 1
          this.getList()
      },
    // 回退
    goBack () {
      this.$router.push('/personal')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .search{
        width: 30px;
        position: absolute;
        right: 30px;
        top: 29px;
    }
    .middle
        position fixed
        top 48px
        width 100%
    /deep/.md-tab-bar
        background-color #ffffff
        border-bottom 1px solid rgba(181,181,181,1)
        .md-tab-bar-item
            color #999999FF!important
            font-size 28px
            font-weight 500
            min-height 80px!important
            padding 0 44px!important
        .md-tab-bar-item.is-active
            color #021955FF!important
        .md-tab-bar-ink
            background-color #021955FF
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .md-example-child-scroll-view-0
        height 600px
        background #FFF
        .scroll-view-item
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 174px
    .scroll-view-container
        .expertItem
            padding 20px 0px
            border 2px solid rgba(153,153,153,0.2)
            border-radius:20px!important
            background-color white!important
            justify-content left!important
            .article
                width auto!important
        .scroll-view-item
            margin-bottom  40px
            border-radius 4px
            display flex
            justify-content space-between
            .underLine
                padding-bottom 40px
                border-bottom 2px solid rgba(153,153,153,0.2)
            .img
                width 260px
            .imgCover
                background-color #ffffff
                height 152px
            .imgCoverExpert
                height 180px!important
            .expert-img
                width 1.8rem
                height 1.8rem
                margin 0 0.3rem
                border-radius 50%
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.md-tab-bar .md-tab-bar-list
        border-top 0!important
    .md-scroll-view
        padding 20px
    .expertTitle
        font-size 24px
        color #021955
        border 2px solid #021955
        border-radius 8px
        padding 10px
        margin-top 12px
</style>
